input[type="checkbox"],
input[type="radio"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 30px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease;
}

input[type="checkbox"]:checked,
input[type="radio"]:checked {
    background-color: #FDC913;
    border: 2px solid #292929;
}

input[type="checkbox"]:checked::after,
input[type="radio"]:checked::after {
    content: "\2713";
    font-size: 14px;
    color: #292929;
    position: relative;
    left: 6px;
    top: 4px;
    font-weight: bold;
}

input[type="radio"] {
    position: relative;
    margin-right: 10px;
    border-radius: 50%;
    border: 2px solid #ccc;
    width: 18px;
    height: 18px;
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease;
}

input[type="radio"]:checked {
    background-color: #FDC913;
    border: 2px solid #FDC913;
}

input[type="radio"]:checked::after {
    content: "";
    width: 10px;
    height: 10px;
    background-color: #292929;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

